.wp-eventkoi {
  #wpbody-content {
    padding-bottom: 0;
  }

  #wpcontent {
    padding-left: 0;
  }
}

.eventkoi-admin {
  font-family: inter, -apple-system, blinkmacsystemfont, "Segoe UI", roboto,
    oxygen-sans, ubuntu, cantarell, "Helvetica Neue", sans-serif;
  font-size: 16px;

  p {
    font-size: 16px;
  }
  li {
    margin-bottom: 0px;
  }
  a {
    &:hover,
    &:focus {
      box-shadow: none;
      color: inherit;
    }
  }
}
